@import '../base/mixins.scss';
.icon {
    display: block;
    a {
        display: inline-block;
        color: black !important;
    }
}

section,
body {
    overflow-x: hidden;
    box-sizing: border-box;
}

.homepage {
    .slide {
        img {
            width: 100%;
        }
    }
    .collect {
        &__skill {
            height: 240px;
            position: relative;
            .center-opt {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .icon {
        a {
            margin: 0 4px;
            .fa-linkedin-square {
                &:hover {
                    color: #0073b1;
                }
            }
            .fa-whatsapp {
                &:hover {
                    color: #53c373;
                }
            }
            .fa-envelope {
                &:hover {
                    color: #e53935;
                }
            }
            .fa-skype {
                &:hover {
                    color: #0078ca;
                }
            }
            .fa-github {
                &:hover {
                    color: gray;
                }
            }
        }
    }
    .js {
        background-position: 0 0;
    }
}